*{
  box-sizing: border-box;
}

body{
  text-align: center;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  height: 100vh;
}

.form{
  margin-top: 25%;
  width: 500px;
  background-color: #eeeeee;
  padding: 0 30px;
  display: grid;
  gap: 20px;
  padding-bottom: 30px;
}

input{
  width: 100%;
  padding: 10px;
}

.otpverify{
  width: 100%;
  display: flex;
  gap:20px;
}

.btn{
  padding: 10px;
  background-color: #82eec1;
  outline: none;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  width: 150px;
}

.otpverify{
  display: none;
}